* {
    font-family: 'Varela Round', sans-serif;
}

#row {
    margin-top: 10px;
    /* padding: 20px 0 100px 0; */
}

#rightside {
    height: 100vh;
    width: 50%;
    float: right;
    padding-top: 290px;
}

#rightside h1 {
    font-weight: 700px;
    font-size: 45px;
    letter-spacing: 1px;
    line-height: 1;
    margin-bottom: 20px;
    display: block;
    color: var(--teal);
}

#rightside p {
    font-size: 20px;
    letter-spacing: 1px;
    display: flex;
    align-items: center;
    color: var(--light-slate);
}

#leftside {
    display: block;
}

@media screen and (max-width: 1080px) {
    #rightside {
        align-items: center;
        float: none;
        margin-left: 25%;
    }

    #rightside h1 {
        font-weight: 350px;
        font-size: 40px;
    }

    #rightside p {
        font-size: 20px;
    }

    #leftside {
        display: none;
    }

}